import * as XLSX from 'xlsx';
import { getBase64 } from '../file-helper';
import { colors } from '../../global/colors-hexadecimal.js';

const sheetConfig = {
  sheet:{
    type: '',
    dimension: '',
    value: '',
  },
  indicator_label: {
    columnRegex: new RegExp("^A"),
    columnStart: "A1"
  },
  initial_value: {
    columnRegex: new RegExp("^B"),
    columnStart: "B1"
  },
  final_value: {
    columnRegex: new RegExp("^C"),
    columnStart: "C1"
  },
  alpha: {
    columnRegex: new RegExp("^D"),
    columnStart: "D1"
  },
  alpha_prime: {
    columnRegex: new RegExp("^E"),
    columnStart: "E1"
  },
  beta: {
    columnRegex: new RegExp("^F"),
    columnStart: "F1"
  },
  success_rates: {
    columnRegex: new RegExp("^G"),
    columnStart: "G1"
  },
  instrumental: {
    columnRegex: new RegExp("^H"),
    columnStart: "H1"
  },
  monitoring: {
    columnRegex: new RegExp("^I"),
    columnStart: "I1"
  },
  rule_of_law: {
    columnRegex: new RegExp("^J"),
    columnStart: "J1"
  },
  min_value: {
    columnRegex: new RegExp("^K"),
    columnStart: "K1"
  },
  max_value: {
    columnRegex: new RegExp("^L"),
    columnStart: "L1"
  },
  indicator_name: {
    columnRegex: new RegExp("^M"),
    columnStart: "M1"
  },
  indicator_color: {
    columnRegex: new RegExp("^N"),
    columnStart: "N1"
  },
}

const validateIndicators = async (format, f, indicator_sheet = 'template') => {
  let error = false;
  const errors = new Set();

  const generate_indicator_color = (length) => {
    if (length > colors.length) {
      var remainder = length - colors.length
      color = colors.slice(0,remainder);
      while (remainder >= 0) {
        color = color.concat(colors.slice(0,remainder));
        remainder -= colors.length;
      }
    } else {
      color = colors.slice(0,length);
    }
  }

  const isLeftValueLowerThanRight = (a, b) =>
    a.length === b.length &&
    a.every((v, i) => v < b[i]);

  const anyValueEqual = (a, b) =>
    a.length === b.length &&
    a.some((v, i) => v === b[i]);

  const isNumber = (column) => column['t'] === 'n';
  const isPositive = (value) => value['v'] >= 0;
  const isBetweenZeroAndOne = (value) => value['v'] >= 0 && value['v'] <= 1;
  const isOneOrZero = (value) => value['v'] === 0 || value['v'] === 1;
  const isColor = (value) => /^#[0-9A-F]{6}$/i.test(value['v'])

  const addError = (variable, cell, type) => {
    if (type === 'type') {
      errors.add(`All values should be numeric - ${variable} ${cell}`);
    } else if (type === 'dimension') {
      errors.add(`The number of values should be the same as the number of values in column ${variable}`);
    } else if (type === 'R') {
      errors.add('At least one value must be > 0');
    } else if (type === 'value') {
      errors.add(`Each value should not contain more than 12 characters - ${cell}`);
    } else if (type === 'unique') {
      errors.add(`${variable} should all be unique values`);
    } else if (type === 'empty') {
      errors.add(`The uploaded file structure does not meet the template format or is empty - ${variable}`);
    } else if (type === 'wrong_sheet') {
      errors.add(`The file uploaded do not contain a sheet with the name: ${indicator_sheet} `);
    } else if (type === 'success_rates') {
      errors.add(`You must have values in alpha, alpha_prime and beta if ${variable} is empty`);
    } else if (type === 'color') {
      errors.add('Color values must be proper hex values');
    } else if (type === 'higher') {
      errors.add(`${variable} has a higher value than ${cell}`);
    } else if (type === 'lower') {
      errors.add(`${variable} has a lower value than ${cell}`);
    } else if (type === 'identical') {
      errors.add(`Values of ${variable} cannot be the same as ${cell}`);
    } else {
      errors.add(`All values should be between 0 and 1 - ${variable}`);
    }
    error = true;
  };

  if (format !== 'xlsx' && format !== 'xls') {
    throw new Error("Wrong format file");
  } else {
    const f64 = await getBase64(f);
    const file = XLSX.read(f64, { type: 'base64' });
    var I0 = [];
    var IF = [];
    var alpha = [];
    var alpha_prime = [];
    var beta = [];
    var success_rates = [];
    var R = [];
    var qm = [];
    var rl = [];
    var Imax = [];
    var Imin = [];
    var G = [];
    var ID = [];
    var name = [];
    var color = [];

    // Verify if exists NaN values
    const indicators = file.Sheets[indicator_sheet];
    if (!indicators || indicators.length === 0) {
      addError('sheet', null, 'wrong_sheet');
    } else {
      Object.keys(indicators).map((k) => {
        if (sheetConfig.indicator_label.columnRegex.test(k) && k !== sheetConfig.indicator_label.columnStart) {
          if (indicators[k]['t'] === 's') {
            if (indicators[k]['v'].length > 12) {
              addError('indicator_label', k, 'value');
            } else {
              ID.push(indicators[k]['v']);
            }
          } else {
            addError('indicator_label', k, 'type');
          }
        }

        if (sheetConfig.initial_value.columnRegex.test(k) && k !== sheetConfig.initial_value.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? I0.push(indicators[k]['v']) : addError('initial_value', k, 'type');
        }

        if (sheetConfig.final_value.columnRegex.test(k) && k !== sheetConfig.final_value.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? IF.push(indicators[k]['v']) : addError('final_value', k, 'type');
        }

        if (sheetConfig.alpha.columnRegex.test(k) && k !== sheetConfig.alpha.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? alpha.push(indicators[k]['v']) : addError('alpha', k, 'type');
        }

        if (sheetConfig.alpha_prime.columnRegex.test(k) && k !== sheetConfig.alpha_prime.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? alpha_prime.push(indicators[k]['v']) : addError('alpha_prime', k, 'type');
        }

        if (sheetConfig.beta.columnRegex.test(k) && k !== sheetConfig.beta.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? beta.push(indicators[k]['v']) : addError('beta', k, 'type');
        }

        if (sheetConfig.success_rates.columnRegex.test(k) && k !== sheetConfig.success_rates.columnStart) {
          if (isNumber(indicators[k]) && isBetweenZeroAndOne(indicators[k])) {
            success_rates.push(indicators[k]['v'])
          } else {
            addError('success_rates', k, 'type');
          }
        }

        if (sheetConfig.instrumental.columnRegex.test(k) && k !== sheetConfig.instrumental.columnStart) {
          if (isNumber(indicators[k]) && isOneOrZero(indicators[k])) {
            R.push(indicators[k]['v']);
          } else {
            addError('instrumental', k, 'type');
          }
        }

        if (sheetConfig.monitoring.columnRegex.test(k) && k !== sheetConfig.monitoring.columnStart) {
          if (isNumber(indicators[k]) && isBetweenZeroAndOne(indicators[k])) {
            qm.push(indicators[k]['v']);
          } else {
            addError('monitoring', k, 'type');
          }
        }

        if (sheetConfig.rule_of_law.columnRegex.test(k) && k !== sheetConfig.rule_of_law.columnStart) {
          if (isNumber(indicators[k]) && isBetweenZeroAndOne(indicators[k])) {
            rl.push(indicators[k]['v']);
          } else {
            addError('rule_of_law', k, 'type');
          }
        }

        if (sheetConfig.min_value.columnRegex.test(k) && k !== sheetConfig.min_value.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? Imin.push(indicators[k]['v']) : addError('min_value', k, 'type');
        }

        if (sheetConfig.max_value.columnRegex.test(k) && k !== sheetConfig.max_value.columnStart) {
          isNumber(indicators[k]) && isPositive(indicators[k]) ? Imax.push(indicators[k]['v']) : addError('max_value', k, 'type');
        }

        if (sheetConfig.indicator_name.columnRegex.test(k) && k !== sheetConfig.indicator_name.columnStart) {
          name.push(indicators[k]['v'])
        }

        if (sheetConfig.indicator_color.columnRegex.test(k) && k !== sheetConfig.indicator_color.columnStart) {
          isColor(indicators[k]) ? color.push(indicators[k]['v']) : addError('color', k, 'color');
        }
      })

      if (ID.length === 0) {
        addError('indicator_label', null, 'empty')
      }

      if (new Set(ID).size !== ID.length) {
        addError('indicator_label', null, 'unique')
      }

      if (I0.length === 0) {
        addError('initial_value', null, 'empty');
      } else {
        // Verify the dimensions of arrays
        if (I0.length !== qm.length && qm.length !== 0) {
          addError('monitoring', null, 'dimension');
        }

        if (!success_rates.length && !(alpha.length && alpha_prime.length && beta.length)) {
          addError('success_rates', null, 'success_rates');
        }

        if (I0.length !== alpha.length && alpha.length > 0) {
          addError('alpha', null, 'dimension');
        }
        if (I0.length !== alpha_prime.length && alpha_prime.length > 0) {
          addError('alpha_prime', null, 'dimension');
        }
        if (I0.length !== beta.length && beta.length > 0) {
          addError('beta', null, 'dimension');
        }
        if (I0.length !== R.length) {
          addError('instrumental', null, 'dimension');
        }

        if (I0.length !== rl.length && rl.length !== 0) {
          addError('rule_of_law', null, 'dimension');
        }

        if (I0.length !== Imax.length && Imax.length > 0) {
          addError('max_value', null, 'dimension');
        }

        if (I0.length !== Imin.length && Imin.length > 0) {
          addError('min_value', null, 'dimension');
        }

        if (!isLeftValueLowerThanRight(Imin, I0) && Imin.length > 0) {
          addError('min_value', 'initial_value', 'higher');
        }

        if (!isLeftValueLowerThanRight(Imin, IF) && Imin.length > 0) {
          addError('min_value', 'final_value', 'higher');
        }

        if (!isLeftValueLowerThanRight(I0, Imax)) {
          addError('max_value', 'initial_value', 'lower');
        }

        if (!isLeftValueLowerThanRight(IF, Imax)) {
          addError('max_value', 'final_value', 'lower');
        }

        if (anyValueEqual(I0, IF)) {
          addError('initial_value', 'final_value', 'identical');
        }
      }
      // eslint-disable-next-line no-undef
      if (!(nj.sum(R) > 0)) {
        addError('instrumental', null, 'zero');
      }
    }
  }
  if (!error) {
    if(color.length === 0) generate_indicator_color(I0.length);
    if (qm.length === 0) qm = Array.from({ length: I0.length }, () => 0.5);
    if (rl.length === 0) rl = Array.from({ length: I0.length }, () => 0.5);
    return { I0, IF, alpha, alpha_prime, beta, success_rates, R, qm, rl, Imax, Imin, G, ID, color, name }
  } else {
    return { error: true, errors };
  }
}

export { validateIndicators };
