import React from 'react';
import { Box, Button, Container, Grid, Typography } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import indicatorsTemplate from '../../../assets/templates/template_indicators.xlsx';
import { texts_indicators as texts } from '../../../global/texts/texts-indicators-page.js'
import { CustomBreadcrumbs } from '../breadcrumbs';
import { MediaUploader } from '../../../common/media-uploader/media-uploader';
import { FileValidation } from './validations/fileValidation';
import { validateIndicators } from '../../../helpers/validations/validateIndicators';
import { getFileExtention } from '../../../helpers/file-helper';
import { SiteTitle } from '../../../components/SiteTitle';

const Indicators = (props) => {
  const { nextStep, indicatorsFile, setIndicatorsFile , simulationState, setIndicatorsResult} = props;
  const [verified, setVerified] = React.useState(false);
  const [validationResult, setValidationResult] = React.useState(null);

  const uploadFile = async (file) => {
    if (file && file.name) {
      const extention = getFileExtention(file);
      const fileValidation = await validateIndicators(extention, file);
      setIndicatorsFile(file);
      if (
        !fileValidation.error
      ) {
        setVerified(true);
        setValidationResult(fileValidation);
        setIndicatorsResult(fileValidation);
      } else {
        setVerified(false);
        setValidationResult(fileValidation);
      }
    } else {
      setIndicatorsFile([]);
      setVerified(false);
      setValidationResult(null);
    }
  }

  React.useEffect(() => {
    const script = document.createElement('script');
    script.src = "/numjs.min.js";
    script.async = true;
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    }
  }, []);

  return (
    <Container
      maxWidth="xl"
      sx={{
        paddingY: (theme) => [ theme.spacing(4), theme.spacing(4), theme.spacing(8) ]
      }}
    >
      <Grid container rowSpacing={4} columnSpacing={8} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
        <Grid item xs={12} md={5}>
          <SiteTitle />
        </Grid>
        <Grid item xs={12} md={7}>
          <CustomBreadcrumbs selected={simulationState} />
        </Grid>
        <Grid item xs={12} md={5}>
          <Typography variant='h1'>
            Indicators
          </Typography>
          <Typography variant='body1' mt={4}>
            { texts.INFORMATION_DESCRIPTION }
          </Typography>
          <Typography variant='body1' mt={4} mb={2}>
            { texts.TEMPLATE_DESCRIPTION }
          </Typography>
          <Button variant='outlined' href={indicatorsTemplate}>
            Download template
          </Button>
        </Grid>
        <Grid item xs={12} md={7}>
          <MediaUploader
            maxFiles={1}
            previewsPerLine={1}
            supportedExtensions={['xlsx', 'xls']}
            onFileSelection={(files) => {
              if (files) {
                uploadFile(files[0]);
              } else {
                uploadFile([]);
              }
            }}
          />
          {indicatorsFile
          && validationResult
          && indicatorsFile.name
          && (
            <FileValidation component="indicator" verified={verified} validationResult={validationResult} />
          )}
          <Box mt={4} textAlign="right">
            <Button
              variant='contained'
              size='large'
              onClick={() => nextStep()}
              disabled={!verified}
              endIcon={<NavigateNextIcon />}
            >
              Next
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Container>
  )
}

export { Indicators };

export default Indicators;
